import React from 'react';
import { motion } from 'framer-motion';
import { BarChart3, Bot, ShieldCheck, Zap, PieChart, ArrowRight } from 'lucide-react';

interface LandingProps {
  onLogin: () => void;
  onRegister: () => void;
}

const Landing: React.FC<LandingProps> = ({ onLogin, onRegister }) => {
  return (
    <div className="min-h-screen bg-gradient-to-br from-white via-blue-50 to-indigo-50">
      {/* Nav */}
      <header className="max-w-7xl mx-auto px-6 py-5 flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div className="w-10 h-10 bg-gradient-to-br from-primary-500 to-primary-600 rounded-lg flex items-center justify-center">
            <Bot className="w-6 h-6 text-white" />
          </div>
          <span className="text-xl font-bold text-gray-900">AI Analytics</span>
        </div>
        <div className="flex items-center gap-3">
          <button className="btn-secondary" onClick={onLogin}>Log in</button>
          <button className="btn-primary" onClick={onRegister}>Get started</button>
        </div>
      </header>

      {/* Hero */}
      <main className="max-w-7xl mx-auto px-6 py-16 grid grid-cols-1 lg:grid-cols-2 gap-10 items-center">
        <div>
          <motion.h1
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
            className="text-4xl md:text-5xl font-extrabold text-gray-900 leading-tight"
          >
            AI-Powered Data Analysis for Small Businesses
          </motion.h1>
          <motion.p
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.1 }}
            className="mt-4 text-lg text-gray-600"
          >
            Make smarter decisions with real-time dashboards, analytics, and an AI assistant that turns data into action.
          </motion.p>

          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.15 }}
            className="mt-8 flex flex-wrap gap-3"
          >
            <button className="btn-primary flex items-center gap-2" onClick={onRegister}>
              Start free
              <ArrowRight className="w-4 h-4" />
            </button>
            <button className="btn-secondary" onClick={onLogin}>Log in</button>
          </motion.div>

          <div className="mt-10 grid grid-cols-1 sm:grid-cols-3 gap-4">
            <div className="card">
              <div className="flex items-center gap-3">
                <BarChart3 className="w-5 h-5 text-primary-600" />
                <p className="font-semibold text-gray-900">Live Dashboards</p>
              </div>
              <p className="text-sm text-gray-600 mt-2">Track revenue, customers, and inventory in one place.</p>
            </div>
            <div className="card">
              <div className="flex items-center gap-3">
                <PieChart className="w-5 h-5 text-green-600" />
                <p className="font-semibold text-gray-900">Actionable Insights</p>
              </div>
              <p className="text-sm text-gray-600 mt-2">See trends and opportunities with clear visuals.</p>
            </div>
            <div className="card">
              <div className="flex items-center gap-3">
                <ShieldCheck className="w-5 h-5 text-indigo-600" />
                <p className="font-semibold text-gray-900">Secure by Design</p>
              </div>
              <p className="text-sm text-gray-600 mt-2">Built with modern best practices and reliability.</p>
            </div>
          </div>
        </div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.1 }}
          className="card"
        >
          <div className="bg-gradient-to-br from-primary-50 to-blue-50 rounded-lg p-6 border border-primary-100">
            <div className="flex items-center gap-3">
              <Zap className="w-5 h-5 text-primary-600" />
              <p className="font-semibold text-gray-900">What you get</p>
            </div>
            <ul className="mt-4 space-y-2 text-sm text-gray-700 list-disc pl-5">
              <li>Dashboard, Inventory, Analytics, Customers, Sales</li>
              <li>AI chatbot for instant answers</li>
              <li>Interactive charts and reports</li>
              <li>Responsive UI and fast performance</li>
            </ul>
          </div>
        </motion.div>
      </main>

      <footer className="max-w-7xl mx-auto px-6 py-10 text-sm text-gray-500">
        © {new Date().getFullYear()} AI Analytics. All rights reserved.
      </footer>
    </div>
  );
};

export default Landing;

